import React, { useState } from 'react';
import { PieChart, Pie, Cell, Tooltip } from 'recharts';
import html2canvas from 'html2canvas';

const COLORS = ['#8884d8', '#82ca9d', '#ffc658', '#ff8042', '#8dd1e1', '#d0ed57', '#a4de6c', '#d084ff'];

const defaultIdentities = [
  { category: 'Sex', identities: ['male', 'female', 'intersex'] },
  { category: 'Sexuality', identities: ['heterosexual', 'homosexual', 'bisexual/pansexual', 'asexual'] },
  { category: 'Skin Color', identities: ['white', 'different shade', 'dark'] },
  { category: 'SES/Class', identities: ['wealthy', 'middle class', 'working class', 'poor'] },
  { category: 'Ability', identities: ['able-bodied', 'allergy/auto', 'significant disability'] },
  { category: 'Age', identities: ['35-55', 'appropriate age', 'elder/youth'] },
  { category: 'Body Size', identities: ['average height/size', 'obese/short'] },
  { category: 'Citizenship', identities: ['citizen', 'documented', 'undocumented'] },
  { category: 'Education', identities: ['post-secondary', 'secondary', 'elementary'] },
  { category: 'Employment', identities: ['FT permanent', 'Part-time/Precarious', 'unemployed'] },
  { category: 'Family Status', identities: ['married', 'couple', 'single'] },
  { category: 'Gender', identities: ['CIS man', 'CIS woman', 'trans/non-binary'] },
  { category: 'Housing', identities: ['own', 'rent/boarders', 'unhoused/transient'] },
  { category: 'Indigeneity', identities: ['settler', 'non-status', 'Indigenous'] },
  { category: 'Language', identities: ['English', 'learned English', 'limited/no English'] },
  { category: 'Literacy', identities: ['fluent', 'Functional Literacy', 'limited/no Literacy'] },
  { category: 'Location', identities: ['Urban', 'Stable', 'rural'] },
  { category: 'Mental Health', identities: ['Robust', 'stable', 'significant mental illness'] },
  { category: 'Neurotype', identities: ['Neurotypical', 'some neurodiversity', 'significant difference'] },
  { category: 'Religion', identities: ['Dominant', 'Agnostic/Atheist', 'non-dominant'] }
];

export default function IdentityWheel() {
  const [selectedIdentities, setSelectedIdentities] = useState([]);
  const [currentCategory, setCurrentCategory] = useState('');
  const [currentIdentity, setCurrentIdentity] = useState('');
  const [importance, setImportance] = useState(50);
  const [editingIndex, setEditingIndex] = useState(null);
  const [chartSize, setChartSize] = useState(500);

  const getPowerPosition = (category, identity) => {
    const categoryData = defaultIdentities.find(cat => cat.category === category);
    if (!categoryData) return 0;
    const index = categoryData.identities.indexOf(identity);
    const total = categoryData.identities.length;
    return index / (total - 1);
  };

  const addIdentity = () => {
    if (currentCategory && currentIdentity) {
      const powerPosition = getPowerPosition(currentCategory, currentIdentity);
      const newEntry = {
        name: `${currentCategory}: ${currentIdentity}`,
        category: currentCategory,
        identity: currentIdentity,
        rawValue: importance,
        powerPosition
      };
      if (editingIndex !== null) {
        const updated = [...selectedIdentities];
        updated[editingIndex] = newEntry;
        setSelectedIdentities(updated);
        setEditingIndex(null);
      } else {
        setSelectedIdentities([...selectedIdentities, newEntry]);
      }
      setCurrentCategory('');
      setCurrentIdentity('');
      setImportance(50);
    }
  };

  const handleSliceClick = (index) => {
    const slice = selectedIdentities[index];
    const [category, identity] = slice.name.split(': ').map(s => s.trim());
    setCurrentCategory(category);
    setCurrentIdentity(identity);
    setImportance(slice.rawValue);
    setEditingIndex(index);
  };

  const total = selectedIdentities.reduce((sum, entry) => sum + entry.rawValue, 0);
  let currentAngle = 0;
  const maxOuterRadius = chartSize / 2 - 20;
  const minOuterRadius = chartSize / 4;

  const pieSlices = selectedIdentities
    .slice()
    .sort((a, b) => a.powerPosition - b.powerPosition || b.rawValue - a.rawValue)
    .map((entry, index) => {
      const percent = total > 0 ? (entry.rawValue / total) * 100 : 0;
      const angle = (percent / 100) * 360;
      const startAngle = currentAngle;
      const endAngle = currentAngle - angle;
      currentAngle -= angle;
      const outerRadius = minOuterRadius + (1 - entry.powerPosition) * (maxOuterRadius - minOuterRadius);

      return {
        ...entry,
        value: angle,
        percent,
        startAngle,
        endAngle,
        outerRadius,
        fill: COLORS[index % COLORS.length],
        originalIndex: selectedIdentities.indexOf(entry)
      };
    });

  const saveToLocal = () => localStorage.setItem('identityWheelData', JSON.stringify(selectedIdentities));
  const loadFromLocal = () => {
    const data = localStorage.getItem('identityWheelData');
    if (data) setSelectedIdentities(JSON.parse(data));
  };
  const exportAsImage = () => {
    const chartElement = document.getElementById('chart-container');
    html2canvas(chartElement).then(canvas => {
      const link = document.createElement('a');
      link.download = 'identity-wheel.png';
      link.href = canvas.toDataURL();
      link.click();
    });
  };

  return (
    <div style={{ padding: '2rem', fontFamily: 'sans-serif' }}>
      <h1>Identity Wheel Maker</h1>
      <div style={{ display: 'grid', gap: '1rem', marginBottom: '2rem' }}>
        <label>Chart Size:
          <input type="range" min="300" max="700" step="50" value={chartSize} onChange={e => setChartSize(Number(e.target.value))} /></label>

        <label>Identity Category:
          <select value={currentCategory} onChange={e => setCurrentCategory(e.target.value)}>
            <option value="">--Select--</option>
            {defaultIdentities.map(cat => (
              <option key={cat.category} value={cat.category}>{cat.category}</option>
            ))}
          </select></label>

        {currentCategory && (
          <label>Specific Identity:
            <select value={currentIdentity} onChange={e => setCurrentIdentity(e.target.value)}>
              <option value="">--Select--</option>
              {defaultIdentities.find(cat => cat.category === currentCategory)?.identities.map(identity => (
                <option key={identity} value={identity}>{identity}</option>
              ))}
            </select></label>
        )}

        <label>Importance (slice size):
          <input type="range" min="10" max="100" step="5" value={importance} onChange={e => setImportance(Number(e.target.value))} /></label>

        <button onClick={addIdentity}>{editingIndex !== null ? 'Update Identity' : 'Add Identity'}</button>
        <button onClick={saveToLocal}>Save</button>
        <button onClick={loadFromLocal}>Load</button>
        <button onClick={exportAsImage}>Export Image</button>
      </div>

      <h2>Your Identity Wheel</h2>
      <div id="chart-container">
        <PieChart width={chartSize} height={chartSize}>
          {pieSlices.map((slice, i) => (
            <Pie
              key={`slice-${i}`}
              data={[slice]}
              dataKey="value"
              nameKey="name"
              cx="50%"
              cy="50%"
              innerRadius={30}
              outerRadius={slice.outerRadius}
              startAngle={slice.startAngle}
              endAngle={slice.endAngle}
              labelLine={false}
              onClick={() => handleSliceClick(slice.originalIndex)}
              label={({ cx, cy, midAngle, outerRadius }) => {
                const RADIAN = Math.PI / 180;
                const angle = -midAngle;
                const radius = outerRadius * 0.5;
                const x = cx + radius * Math.cos(angle * RADIAN);
                const y = cy + radius * Math.sin(angle * RADIAN);

                const categoryLines = slice.category.toUpperCase().split(' ');

                return (
                  <g>
                    <text
                      x={x}
                      y={y}
                      textAnchor="middle"
                      dominantBaseline="middle"
                      fontSize={10}
                      transform={`rotate(${angle}, ${x}, ${y})`}
                    >
                      {slice.identity}
                    </text>
                    <text
                      x={cx + (outerRadius + 10) * Math.cos(angle * RADIAN)}
                      y={cy + (outerRadius + 10) * Math.sin(angle * RADIAN)}
                      textAnchor="middle"
                      dominantBaseline="middle"
                      fontSize={10}
                      transform={`rotate(${angle + 90}, ${cx + (outerRadius + 10) * Math.cos(angle * RADIAN)}, ${cy + (outerRadius + 10) * Math.sin(angle * RADIAN)})`}
                    >
                      <tspan x={cx + (outerRadius + 10) * Math.cos(angle * RADIAN)} dy="-0.6em">
                        {categoryLines[0]}
                      </tspan>
                      {categoryLines[1] && (
                        <tspan x={cx + (outerRadius + 10) * Math.cos(angle * RADIAN)} dy="1.2em">
                          {categoryLines[1]}
                        </tspan>
                      )}
                    </text>
                  </g>
                );
              }}
            >
              <Cell fill={slice.fill} />
            </Pie>
          ))}
          <Tooltip
            formatter={(value, name, props) => [
              `${(value / 360 * 100).toFixed(1)}% of total importance`,
              props.payload.name + ` (Power Position: ${((1 - props.payload.powerPosition) * 100).toFixed(0)}%)`
            ]}
          />
        </PieChart>
      </div>
    </div>
  );
}
